"use client";

import { useQuery } from "convex/react";
import { api } from "../convex/_generated/api";
import * as d3 from "d3";

import Heatmap from "../components/heatmap"
import ColorLegend from "../components/colorscale"

const colorScale = d3
  .scaleSequential()
  .interpolator(d3.interpolateRdYlGn)
  .domain([16, 0]);

export default function Home() {
  const queue_times = useQuery(api.queue_times.get)
  
  return (
    <main>
      <h1>How quickly can you get a GPU on Modal?</h1>
      <p>Sampling TTYCR (time to your code running) on each of our GPU types üèéÔ∏è</p>
      <div className="heatmap-container">
        {queue_times && <Heatmap
          width={1600}
          height={400}
          data={queue_times}
          colorScale={colorScale}
        />
        }
        <ColorLegend
          width={700}
          height={100}
          colorScale={colorScale}
        />
      </div>
    </main>
  );
}